import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import GuideExportCard from "./GuideExportCard";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import style from "../../FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  useGuideExportExcelList,
  useGuideExportMenteeList,
} from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
const GuideExport = ({ gid, instituteId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [openStep, setOpenStep] = useState("");

  const [guideExportMenteeList] = useGuideExportMenteeList();
  const { guideExportExcelList, guideExportExcelListLoading } =
    useGuideExportExcelList({
      data: {
        gid: gid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !gid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (guideExportExcelList?.list?.length) {
      let obj = nextPage(guideExportExcelList?.list?.length);
      setState(() => obj);
    }
  }, [guideExportExcelList?.list?.length]);
  const onSearchEvent = (e) => {
    if (
      !guideExportExcelListLoading &&
      guideExportExcelList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  const onClose = () => {
    setOpenStep("");
  };

  const onExport = () => {
    if (gid) {
      setDisabled((pre) => !pre);
      guideExportMenteeList({
        gid: gid,
      })
        .then((res) => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("data_export")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                    style={{
                      minWidth: "250px",
                    }}
                  />
                </div>
              </section>
              <CreateButton
                label="export_mentee"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onExport}
              />
              {/* <CreateButton
                label="export_attendance"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setOpenStep(1)}
              /> */}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {guideExportExcelList?.list?.map((excel, index) =>
            guideExportExcelList?.list?.length === index + 1 ? (
              <div key={excel?._id} ref={ref}>
                <GuideExportCard excel={excel} instituteId={instituteId} />
              </div>
            ) : (
              <GuideExportCard
                excel={excel}
                key={excel?._id}
                instituteId={instituteId}
              />
            )
          )}
          {guideExportExcelListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            guideExportExcelList?.list?.length === 0 && (
              <EmptyMessage
                title="No excel found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            guideExportExcelList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No excel added."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {/* {openStep === 1 ? (
        <MentorAttendanceExport onClose={onClose} mid={mid} />
      ) : null} */}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default GuideExport;
