import { useState } from "react";
import { useGuideRemoveMentee } from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";

const GuideMenteeListCardMenu = ({ gid, onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [guideRemoveMentee] = useGuideRemoveMentee();

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      guideRemoveMentee({
        gid: gid,
        bodyParameter: { student_list: [menuData?._id] },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onDelete}>{t("remove")}</h6>
    </ModalMenu>
  );
};

export default GuideMenteeListCardMenu;
