import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import dayjs from "dayjs";
import { Link, useLocation, useParams } from "react-router-dom";
import StaffMentorAddMeeting from "../../../../MembersTab/staff/Mainbody/StaffMentor/StaffMentorAddMeeting";
const GuideMeetingCard = ({ meeting_detail, onRefetch, gid }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const [open, setOpen] = useState(false);

  const onToggle = (e) => {
    if (e) {
      e.preventDefault();
    }
    setOpen((pre) => !pre);
  };

  return (
    <>
      {meeting_detail?.creation_status === "SCHEDULE" ? (
        <Link
          className={style.sf_card}
          to={`/q/${params.username}/member?a=guide&type=meeting-detail`}
          state={{
            ...getQuery.state,
            meeting_detail: meeting_detail,
          }}
        >
          <div className={style.sf_card_contatiner}>
            <img
              alt="Student Avatar"
              src="/images/member_tab/student/student-query-icon.svg"
              loading="lazy"
              style={{
                width: "2.5rem",
                height: "2.5rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6>{meeting_detail?.agenda ?? ""}</h6>
              <p>
                {dayjs(meeting_detail?.meeting_date).format("DD/MM/YYYY")},
                {meeting_detail?.meeting_time ?? ""}
              </p>
            </div>
          </div>
          {/* <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onToggle}
          /> */}
        </Link>
      ) : (
        <Link
          className={style.sf_card}
          to={`/q/${params.username}/member?a=guide&type=meeting-detail`}
          state={{
            ...getQuery.state,
            meeting_detail: meeting_detail,
          }}
        >
          <div className={style.sf_card_contatiner}>
            <img
              alt="Student Avatar"
              src="/images/member_tab/student/student-query-icon.svg"
              loading="lazy"
              style={{
                width: "2.5rem",
                height: "2.5rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6>{meeting_detail?.agenda ?? ""}</h6>
              <p>
                {dayjs(meeting_detail?.meeting_date).format("DD/MM/YYYY")},
                {meeting_detail?.meeting_time}
              </p>
            </div>
          </div>
        </Link>
      )}

      {open && (
        <StaffMentorAddMeeting
          onClose={onToggle}
          mid={gid}
          onRefetch={onRefetch}
          type="EDIT"
          meetingDetail={meeting_detail}
          isGuide
        />
      )}
    </>
  );
};

export default GuideMeetingCard;
