import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { useTranslation } from "react-i18next";
import GuideMenteeListCardMenu from "./GuideMenteeListCardMenu";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import { Link, useParams } from "react-router-dom";
const GuideMenteeListCard = ({ student, onRefetch, gid }) => {
  const { t } = useTranslation();
  const [menuData, setMenuData] = useState("");
  const params = useParams();
  const onClose = () => {
    setMenuData("");
  };
  const onMenu = (e) => {
    e.preventDefault();
    setMenuData(student);
  };

  return (
    <>
      <Link
        to={`/q/${params.username}/student/profile`}
        state={{ sid: student?._id, viewAs: "SUBJECT_TEACHER" }}
        className={style.sf_card}
        style={{
          border: "none",
          borderBottom: "0.5px solid #cacaca",
          padding: "0.6rem 1rem",
          marginTop: "0",
          borderRadius: "0",
        }}
      >
        <div className={style.sf_card_contatiner}>
          <img
            alt="Student Avatar"
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            style={{
              width: "2.5rem",
              height: "2.5rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>{`${student?.studentFirstName} ${
              student?.studentMiddleName ? student?.studentMiddleName + " " : ""
            }${student?.studentLastName}`}</h6>
            <p>
              {t("roll_number")} {": "} {student?.studentROLLNO ?? ""}
            </p>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
        />
      </Link>

      {menuData && (
        <GuideMenteeListCardMenu
          onClose={onClose}
          onRefetch={onRefetch}
          menuData={menuData}
          gid={gid}
        />
      )}
    </>
  );
};

export default GuideMenteeListCard;
