import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import {
  useGuideAddMentee,
  useGuideMenteeList,
} from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddStudent from "../../../../MembersTab/staff/Mainbody/DepartmentHead/DepartmentMentor/DepartmentMentorDetail/AddStudent";
import GuideMenteeListCard from "./GuideMenteeListCard";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const GuideMenteeList = ({ gid, departmentId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [disabled, setDisabled] = useState(false);

  const [guideAddMentee] = useGuideAddMentee();
  const { guideMenteeList, guideMenteeListLoading, guideMenteeListRefetch } =
    useGuideMenteeList({
      data: {
        gid: gid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !gid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (guideMenteeList?.list?.length) {
      let obj = nextPage(guideMenteeList?.list?.length);
      setState(() => obj);
    }
  }, [guideMenteeList?.list]);
  const onSearchEvent = (e) => {
    if (!guideMenteeListLoading && guideMenteeList?.list?.length >= 0) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    guideMenteeListRefetch();
  }, [guideMenteeListRefetch]);
  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onAddMentee = (data) => {
    if (data?.length > 0 && gid) {
      onToggle();
      setDisabled((pre) => !pre);
      guideAddMentee({
        gid: gid,
        bodyParameter: {
          student_list: data,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("guide_mentee")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
            <CreateButton
              label="add_mentee"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {guideMenteeList?.list?.map((student, index) =>
          guideMenteeList?.list?.length === index + 1 ? (
            <div key={student?._id} ref={ref}>
              <GuideMenteeListCard
                student={student}
                onRefetch={onRefetch}
                gid={gid}
              />
            </div>
          ) : (
            <GuideMenteeListCard
              student={student}
              key={student?._id}
              onRefetch={onRefetch}
              gid={gid}
            />
          )
        )}
        {guideMenteeListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          guideMenteeList?.list?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          guideMenteeList?.list?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <AddStudent
          onClose={onToggle}
          did={departmentId}
          onGrabData={onAddMentee}
          isGuide
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default GuideMenteeList;
