import { useTranslation } from "react-i18next";
import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import {
  assestsFeedUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";

const GuideMarkAttendanceCard = ({ student, onClick, isSelect, isActive }) => {
  const { t } = useTranslation();
  const onSelect = () => {
    if (isSelect) {
      onClick(student?._id);
    }
  };
  return (
    <>
      <div
        className={style.sf_card}
        style={{
          border: "none",
          borderBottom: "0.5px solid #cacaca",
          padding: "0.6rem 1rem",
          marginTop: "0",
          borderRadius: "0",
        }}
        onClick={onSelect}
      >
        <div className={style.sf_card_contatiner}>
          <img
            alt="Student Avatar"
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            style={{
              width: "2.5rem",
              height: "2.5rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>{`${student?.studentFirstName} ${
              student?.studentMiddleName ? student?.studentMiddleName + " " : ""
            }${student?.studentLastName}`}</h6>
            <p>
              {t("gr_number")} {": "} {student?.studentGRNO ?? ""}
            </p>
          </div>
        </div>
        <img
          src={
            isActive === "present"
              ? `${assestsUtilityUrl}/radio-green.svg`
              : isActive === "absent"
              ? `${assestsUtilityUrl}/radio-red.svg`
              : `${assestsUtilityUrl}/radio-grey.svg`
          }
          alt="user avatar"
        />
      </div>
    </>
  );
};

export default GuideMarkAttendanceCard;
