import { useTranslation } from "react-i18next";
import { useGuideMenteeList } from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
import style from "../../FinanceManager/Income/Income.module.css";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useState } from "react";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoading from "../../../../Loader/QvipleLoading";
import GuideMarkAttendanceCard from "./GuideMarkAttendanceCard";
import CreateButton from "../../../Ui/Button/CreateButton";

const GuideMarkAttendance = ({ gid, onClose, onGrabData }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    present: [],
    absent: [],
  });
  const { guideMenteeList, guideMenteeListLoading } = useGuideMenteeList({
    data: {
      gid: gid,
      page: 1,
      limit: 2000,
      search: "",
    },
    skip: !gid,
  });

  const onAction = (val) => {
    setStep(val);
    setState({
      present: [],
      absent: [],
    });
  };

  const onPresent = (st) => {
    if (state.present?.includes(st)) {
      setState((prev) => ({
        ...prev,
        present: prev.present?.filter((dt) => dt !== st),
      }));
    } else {
      setState((prev) => ({
        ...prev,
        present: [...prev.present, st],
      }));
    }
  };
  const onAbsent = (st) => {
    if (state.present?.includes(st)) {
      setState((prev) => ({
        ...prev,
        absent: prev.absent?.filter((dt) => dt !== st),
      }));
    } else {
      setState((prev) => ({
        ...prev,
        absent: [...prev.absent, st],
      }));
    }
  };

  const onAdd = () => {
    if (state.present?.length > 0) {
      let arr = [];
      if (guideMenteeList?.list?.length > 0) {
        for (let i = 0; i < guideMenteeList?.list?.length; i++) {
          if (state.present?.includes(guideMenteeList?.list[i]?._id)) {
          } else {
            arr.push(guideMenteeList?.list[i]?._id);
          }
        }
      }
      onGrabData({
        present: state.present,
        absent: arr,
      });
    } else {
      let arr = [];
      if (guideMenteeList?.list?.length > 0) {
        for (let i = 0; i < guideMenteeList?.list?.length; i++) {
          if (state.absent?.includes(guideMenteeList?.list[i]?._id)) {
          } else {
            arr.push(guideMenteeList?.list[i]?._id);
          }
        }
      }
      onGrabData({
        present: arr,
        absent: state.absent,
      });
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t("mark_attendance")}</h6>
              </div>
              <div className={style.modal_container_header}>
                <CreateButton
                  label="add"
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 1rem",
                  }}
                  onAction={onAdd}
                />
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <HorizontalTabWrapper>
            {tabList.map((tab) => (
              <HorizontalTabCard
                key={tab?.id}
                tab={tab}
                isActive={tab.id === step ? true : false}
                onAction={onAction}
              />
            ))}
          </HorizontalTabWrapper>
          <BorderBottom
            customStyleBorder={{
              marginTop: "0",
            }}
          />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {step === 1
              ? guideMenteeList?.list?.map((student) => (
                  <GuideMarkAttendanceCard
                    student={student}
                    key={student?._id}
                    isSelect
                    isActive={
                      state.present?.includes(student?._id) ? "present" : ""
                    }
                    onClick={onPresent}
                  />
                ))
              : guideMenteeList?.list?.map((student) => (
                  <GuideMarkAttendanceCard
                    student={student}
                    key={student?._id}
                    isSelect
                    isActive={
                      state.absent?.includes(student?._id) ? "absent" : ""
                    }
                    onClick={onAbsent}
                  />
                ))}
            {guideMenteeListLoading && <QvipleLoading />}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default GuideMarkAttendance;

const tabList = [
  {
    label: "present",
    id: 1,
  },
  {
    label: "absent",
    id: 2,
  },
];
