import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import Switch from "@mui/material/Switch";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  useGuideAddMeeting,
  useGuideAddScheduleMeeting,
  useGuideMeetingUpdate,
} from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
import { slashDateFormat } from "../../../Utils/Functions/dateUtility";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import style from "../../FinanceManager/Income/Income.module.css";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import GuideMarkAttendance from "./GuideMarkAttendance";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
const label = { inputProps: { "aria-label": "Switch demo" } };

const AddGuideMeeting = ({
  onRefetch,
  onClose,
  mid,
  type,
  meetingDetail,
  onParentClose,
  isEdit,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [mark, setMark] = useState(false);
  const [state, setState] = useState({
    agenda: "",
    discussion: "",
    summary: "",
    meeting_alert: true,
    meeting_time: "",
    meeting_date: "",
  });
  const [guideAddMeeting] = useGuideAddMeeting();
  const [guideMeetingUpdate] = useGuideMeetingUpdate();
  const [guideAddScheduleMeeting] = useGuideAddScheduleMeeting();

  useEffect(() => {
    if (type === "EDIT") {
      setState((prev) => ({
        ...prev,
        agenda: meetingDetail?.agenda ?? "",
        discussion: meetingDetail?.discussion ?? "",
        summary: meetingDetail?.summary ?? "",
        meeting_alert: meetingDetail?.meeting_alert ?? false,
        meeting_time: meetingDetail?.meeting_time ?? "",
        meeting_date: meetingDetail?.meeting_date ?? "",
      }));
    }
  }, [type, meetingDetail]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onAddMeeting = (data) => {
    if (type === "EDIT") {
      if (meetingDetail?._id) {
        setDisabled((pre) => !pre);
        guideMeetingUpdate({
          meid: meetingDetail?._id,
          bodyParameter: {
            ...state,
            p_array: data?.present,
            a_array: data?.absent,
            meeting_time: timeFormatTable(state.meeting_time),
          },
        })
          .then(() => {
            onRefetch();
            onClose();
          })
          .catch({});
      }
    } else {
      if (mid) {
        setDisabled((pre) => !pre);
        guideAddMeeting({
          gid: mid,
          bodyParameter: {
            ...state,
            p_array: data?.present,
            a_array: data?.absent,
          },
        })
          .then(() => {
            onRefetch();
            onParentClose();
          })
          .catch({});
      }
    }
  };

  const onDateSelect = (val) => {
    setState((prev) => ({
      ...prev,
      meeting_date: slashDateFormat(val),
    }));
  };

  const onToggle = (e) => {
    setState((prev) => ({
      ...prev,
      meeting_alert: e.target.checked,
    }));
  };
  const onAddMeetingSchedule = () => {
    let validation = requiredField(state, [
      "meeting_alert",
      "p_array",
      "a_array",
      "discussion",
      "summary",
    ]);
    if (existingRequiredField(validation) && mid) {
      setDisabled((pre) => !pre);
      guideAddScheduleMeeting({
        gid: mid,
        bodyParameter: {
          ...state,
          p_array: [],
          a_array: [],
          meeting_time: timeFormatTable(state.meeting_time),
        },
      })
        .then(() => {
          onRefetch();
          onParentClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  const onMark = () => {
    let validation = requiredField(state, [
      "meeting_alert",
      "p_array",
      "a_array",
      "discussion",
      "summary",
      "meeting_date",
      "meeting_time",
    ]);
    if (existingRequiredField(validation)) {
      setMark((pre) => !pre);
    } else {
      setErrorField(validation);
    }
  };

  //   console.info("dsjfhvsfd", errorField);
  return (
    <>
      {mark ? (
        <GuideMarkAttendance
          gid={mid}
          onClose={onMark}
          onGrabData={onAddMeeting}
        />
      ) : (
        <Modal>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6> {t(isEdit ? "update_meeting" : "add_meeting")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div
                style={{
                  marginTop: "0.7rem",
                }}
              >
                <Switch
                  {...label}
                  checked={state.meeting_alert}
                  onChange={onToggle}
                />
                <span>{t("add_meeting_alert")}</span>
              </div>

              <FormInput
                label={t("agenda")}
                placeholder={t("agenda_placeholder")}
                name={"agenda"}
                value={state.agenda}
                type={"text"}
                onChange={onInputChange}
                errorShow={errorField["agenda"]}
              />
              {type === "SCHEDULE" ? (
                <>
                  <FormCalendarInput
                    label={t("date")}
                    placeholder={t("select_date")}
                    name="calendar"
                    value={state.meeting_date}
                    onChange={onDateSelect}
                    errorShow={errorField["meeting_date"]}
                  />

                  <FormInput
                    label={t("time")}
                    placeholder="00:00"
                    name={"meeting_time"}
                    value={state.meeting_time}
                    type={"time"}
                    onChange={onInputChange}
                    errorShow={errorField["meeting_time"]}
                  />
                  <CreateButton label="save" onAction={onAddMeetingSchedule} />
                </>
              ) : (
                <>
                  <FormTextarea
                    label={t("summary")}
                    placeholder={t("summary_placeholder")}
                    name={"summary"}
                    value={state.summary}
                    type={"text"}
                    onChange={onInputChange}
                  />
                  <FormTextarea
                    label={t("discussion")}
                    placeholder={t("discussion_placeholder")}
                    name={"discussion"}
                    value={state.discussion}
                    type={"text"}
                    onChange={onInputChange}
                  />
                  <CreateButton label="mark_attendance" onAction={onMark} />
                </>
              )}
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddGuideMeeting;
const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "Pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "Am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "Pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  return `${hour}:${splitValue[1]} ${meridian}`;
};
