import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import GuideManagerProfile from "./GuideManagerProfile";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import { useTranslation } from "react-i18next";
import GuideMeeting from "./GuideMeeting/GuideMeeting";
import GuideMenteeList from "./GuideMenteeList/GuideMenteeList";
import GuideMeetingDetail from "./GuideMeeting/GuideMeetingDetail";
import GuideExport from "./GuideExport/GuideExport";

const GuideManager = ({ instituteId, staffId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      <UserMemberMainWrapper>
        <UserMemberContainerWrapper>
          <Suspense fallback={<QvipleLoader />}>
            {!searchParams.get("type") ? (
              <GuideManagerProfile
                instituteId={instituteId}
                staffId={staffId}
              />
            ) : searchParams.get("type") === "mentee" ? (
              <GuideMenteeList
                gid={getQuery.state?.gid}
                instituteId={instituteId}
                staffId={staffId}
                departmentId={getQuery.state?.departmentId}
              />
            ) : searchParams.get("type") === "meeting" ? (
              <GuideMeeting
                gid={getQuery.state?.gid}
                instituteId={instituteId}
                staffId={staffId}
                departmentId={getQuery.state?.departmentId}
              />
            ) : searchParams.get("type") === "meeting-detail" ? (
              <GuideMeetingDetail gid={getQuery.state?.gid} />
            ) : searchParams.get("type") === "export" ? (
              <GuideExport gid={getQuery.state?.gid} />
            ) : null}
          </Suspense>
        </UserMemberContainerWrapper>
      </UserMemberMainWrapper>
    </>
  );
};

export default GuideManager;
