import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { useGuideMeetingList } from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import GuideMeetingCard from "./GuideMeetingCard";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import AddGuideMeetingConfig from "./AddGuideMeetingConfig";

const GuideMeeting = ({ gid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const { guideMeetingList, guideMeetingListLoading, guideMeetingListRefetch } =
    useGuideMeetingList({
      data: {
        gid: gid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !gid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (guideMeetingList?.list?.length) {
      let obj = nextPage(guideMeetingList?.list?.length);
      setState(() => obj);
    }
  }, [guideMeetingList?.list]);
  const onSearchEvent = (e) => {
    if (!guideMeetingListLoading && guideMeetingList?.list?.length >= 0) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    guideMeetingListRefetch();
  }, [guideMeetingListRefetch]);
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("guide_meeting")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
            <CreateButton
              label="add_meeting"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {guideMeetingList?.list?.map((meeting_detail, index) =>
          guideMeetingList?.list?.length === index + 1 ? (
            <div key={meeting_detail?._id} ref={ref}>
              <GuideMeetingCard
                meeting_detail={meeting_detail}
                onRefetch={onRefetch}
                gid={gid}
              />
            </div>
          ) : (
            <GuideMeetingCard
              meeting_detail={meeting_detail}
              key={meeting_detail?._id}
              onRefetch={onRefetch}
              gid={gid}
            />
          )
        )}
        {guideMeetingListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          guideMeetingList?.list?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          guideMeetingList?.list?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <AddGuideMeetingConfig
          onClose={onToggle}
          gid={gid}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default GuideMeeting;
