import { useLocation, useParams, useSearchParams } from "react-router-dom";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../Ui/Tab/Square/SquareTabCard";
import {
  assestsMentorUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { memo, useEffect, useState } from "react";
import RectangleTabWrapper from "../../Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../Ui/Tab/Rectangle/RectangleTabCard";
import CreateButton from "../../Ui/Button/CreateButton";
import QvipleLoader from "../../Loader/QvipleLoader";
import InstituteDepartment from "../InstituteDepartment/InstituteDepartment";
import { useTranslation } from "react-i18next";
import style from "../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import {
  useStaffActivateGuide,
  useStaffCheckGuide,
} from "../../Hooks/ApiHooks/GuideManager/guide-manager-api";
const GuideManagerProfile = ({ instituteId, staffId }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    practical: null,
    seminar: null,
    internship: null,
  });
  let height = "9rem";
  let width = "9rem";

  const [staffActivateGuide] = useStaffActivateGuide();
  const { staffCheckGuide, staffCheckGuideRefetch, staffCheckGuideLoading } =
    useStaffCheckGuide({
      data: {
        sid: staffId,
      },
      skip: !staffId,
    });

  useEffect(() => {
    if (staffCheckGuide?.guides?.length > 0) {
      let obj = {
        practical: null,
        seminar: null,
        internship: null,
      };

      for (let ot of staffCheckGuide?.guides) {
        if (ot?.guide_type === "Practical") {
          obj.practical = ot;
        } else if (ot?.guide_type === "Seminar") {
          obj.seminar = ot;
        } else if (ot?.guide_type === "Internship") {
          obj.internship = ot;
        } else {
        }
      }
      setState(obj);
    }
  }, [staffCheckGuide?.guides]);

  const onCloseStep = () => {
    setStep("");
  };

  const onPracticalGuide = (data) => {
    if (data?._id && staffId) {
      setDisabled((pre) => !pre);
      staffActivateGuide({
        did: data?._id,
        bodyParameter: {
          sid: staffId,
          guide_type: "Practical",
          gid: state?.practical?._id ?? "",
        },
      })
        .then(() => {
          staffCheckGuideRefetch();
          setDisabled((pre) => !pre);
          onCloseStep();
        })
        .catch({});
    }
  };
  const onSeminarGuide = (data) => {
    if (data?._id && staffId) {
      setDisabled((pre) => !pre);
      staffActivateGuide({
        did: data?._id,
        bodyParameter: {
          sid: staffId,
          guide_type: "Seminar",
          gid: state?.seminar?._id ?? "",
        },
      })
        .then(() => {
          staffCheckGuideRefetch();
          setDisabled((pre) => !pre);
          onCloseStep();
        })
        .catch({});
    }
  };
  const onInternshipGuide = (data) => {
    if (data?._id && staffId) {
      setDisabled((pre) => !pre);
      staffActivateGuide({
        did: data?._id,
        bodyParameter: {
          sid: staffId,
          guide_type: "Internship",
          gid: state?.internship?._id ?? "",
        },
      })
        .then(() => {
          staffCheckGuideRefetch();
          setDisabled((pre) => !pre);
          onCloseStep();
        })
        .catch({});
    }
  };

  // console.info("instituteId", state);
  return (
    <>
      {staffCheckGuideLoading && <QvipleLoader />}
      <RectangleTabWrapper>
        {tabList.map((tab) => (
          <RectangleTabCard
            key={tab?.id}
            label={tab.label}
            isLink
            isActive={
              tab.id === "project" && !searchParams.get("tab")
                ? true
                : searchParams.get("tab") === tab.id
                ? true
                : false
            }
            parentState={getQuery.state}
            url={`/q/${params.username}/member?a=guide${tab.urlKey}`}
            isAuthorized
          />
        ))}
      </RectangleTabWrapper>
      {!searchParams.get("tab") ? (
        <>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>
                {t("department")}
                {": "} {state?.practical?.department?.dName ?? ""}
              </h6>
              {state?.practical?.department ? (
                <>
                  {state?.practical?.export_collection_count === 0 &&
                  state?.practical?.meetings_count === 0 &&
                  state?.practical?.mentees_count === 0 ? (
                    <CreateButton
                      label="edit_department"
                      onAction={() => setStep(1)}
                      customStyle={{
                        padding: "0.3rem 1rem",
                        margin: "0",
                        minWidth: "fit-content",
                      }}
                    />
                  ) : null}
                </>
              ) : (
                <CreateButton
                  label="select_department"
                  onAction={() => setStep(1)}
                  customStyle={{
                    padding: "0.3rem 1rem",
                    margin: "0",
                    minWidth: "fit-content",
                  }}
                />
              )}
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />

          <SquareTabWrapper dynamicId="589nbhgh">
            <SquareTabCard
              label="students"
              iconUrl={`${assestsMentorUrl}/mentee.svg`}
              url={`/q/${params.username}/member?a=guide&tab=pracitcal&type=mentee`}
              state={{
                ...getQuery.state,
                gid: state?.practical?._id,
                departmentId: state?.practical?.department?._id,
              }}
              isAuthorized={state?.practical?._id ? true : false}
              isVisible={{ all_students: true }}
              authorizedKey="all_students"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            {/* <SquareTabCard
              label="queries"
              iconUrl={`${assestsMentorUrl}/queries.svg`}
              url={`/q/${params.username}/member?a=guide&tab=pracitcal&type=queries`}
              state={{
                ...getQuery.state,
                gid: state?.practical?._id,
                departmentId: state?.practical?.department?._id,
              }}
              isAuthorized={state?.practical?._id ? true : false}
              isVisible={{ bank_details: true }}
              authorizedKey="bank_details"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            /> */}
            <SquareTabCard
              label="meeting"
              iconUrl={`${assestsMentorUrl}/meeting.svg`}
              url={`/q/${params.username}/member?a=guide&tab=pracitcal&type=meeting`}
              state={{
                ...getQuery.state,
                gid: state?.practical?._id,
                departmentId: state?.practical?.department?._id,
              }}
              isAuthorized={state?.practical?._id ? true : false}
              isVisible={{ pending_fees: true }}
              authorizedKey="pending_fees"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="data_export"
              iconUrl={`${assestsUserFinanceUrl}/export.svg`}
              url={`/q/${params.username}/member?a=guide&tab=pracitcal&type=export`}
              state={{
                ...getQuery.state,
                gid: state?.practical?._id,
                departmentId: state?.practical?.department?._id,
              }}
              isAuthorized={state?.practical?._id ? true : false}
              isVisible={{ data_export: true }}
              authorizedKey="data_export"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
          </SquareTabWrapper>
        </>
      ) : searchParams.get("tab") === "seminar" ? (
        <>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>
                {t("department")}
                {": "} {state?.seminar?.department?.dName ?? ""}
              </h6>
              {state?.seminar?.department ? (
                <>
                  {state?.seminar?.export_collection_count === 0 &&
                  state?.seminar?.meetings_count === 0 &&
                  state?.seminar?.mentees_count === 0 ? (
                    <CreateButton
                      label="edit_department"
                      onAction={() => setStep(2)}
                      customStyle={{
                        padding: "0.3rem 1rem",
                        margin: "0",
                        minWidth: "fit-content",
                      }}
                    />
                  ) : null}
                </>
              ) : (
                <CreateButton
                  label="select_department"
                  onAction={() => setStep(2)}
                  customStyle={{
                    padding: "0.3rem 1rem",
                    margin: "0",
                    minWidth: "fit-content",
                  }}
                />
              )}
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <SquareTabWrapper dynamicId="589nbhgh">
            <SquareTabCard
              label="students"
              iconUrl={`${assestsMentorUrl}/mentee.svg`}
              url={`/q/${params.username}/member?a=guide&tab=seminar&type=mentee`}
              state={{
                ...getQuery.state,
                gid: state?.seminar?._id,
                departmentId: state?.seminar?.department?._id,
              }}
              isAuthorized={state?.seminar?._id ? true : false}
              isVisible={{ all_students: true }}
              authorizedKey="all_students"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            {/* <SquareTabCard
              label="queries"
              iconUrl={`${assestsMentorUrl}/queries.svg`}
              url={`/q/${params.username}/member?a=guide&tab=seminar&type=queries`}
              state={{
                ...getQuery.state,
                gid: state?.seminar?._id,
                departmentId: state?.seminar?.department?._id,
              }}
               isAuthorized={state?.seminar?._id?true  :false}
              isVisible={{ bank_details: true }}
              authorizedKey="bank_details"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            /> */}
            <SquareTabCard
              label="meeting"
              iconUrl={`${assestsMentorUrl}/meeting.svg`}
              url={`/q/${params.username}/member?a=guide&tab=seminar&type=meeting`}
              state={{
                ...getQuery.state,
                gid: state?.seminar?._id,
                departmentId: state?.seminar?.department?._id,
              }}
              isAuthorized={state?.seminar?._id ? true : false}
              isVisible={{ pending_fees: true }}
              authorizedKey="pending_fees"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="data_export"
              iconUrl={`${assestsUserFinanceUrl}/export.svg`}
              url={`/q/${params.username}/member?a=guide&tab=seminar&type=export`}
              state={{
                ...getQuery.state,
                gid: state?.seminar?._id,
                departmentId: state?.seminar?.department?._id,
              }}
              isAuthorized={state?.seminar?._id ? true : false}
              isVisible={{ data_export: true }}
              authorizedKey="data_export"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
          </SquareTabWrapper>
        </>
      ) : searchParams.get("tab") === "internship" ? (
        <>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>
                {t("department")}
                {": "} {state?.internship?.department?.dName ?? ""}
              </h6>
              {state?.internship?.department ? (
                <>
                  {state?.internship?.export_collection_count === 0 &&
                  state?.internship?.meetings_count === 0 &&
                  state?.internship?.mentees_count === 0 ? (
                    <CreateButton
                      label="edit_department"
                      onAction={() => setStep(3)}
                      customStyle={{
                        padding: "0.3rem 1rem",
                        margin: "0",
                        minWidth: "fit-content",
                      }}
                    />
                  ) : null}
                </>
              ) : (
                <CreateButton
                  label="select_department"
                  onAction={() => setStep(3)}
                  customStyle={{
                    padding: "0.3rem 1rem",
                    margin: "0",
                    minWidth: "fit-content",
                  }}
                />
              )}
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <SquareTabWrapper dynamicId="589nbhgh">
            <SquareTabCard
              label="students"
              iconUrl={`${assestsMentorUrl}/mentee.svg`}
              url={`/q/${params.username}/member?a=guide&tab=internship&type=mentee`}
              state={{
                ...getQuery.state,
                gid: state?.internship?._id,
                departmentId: state?.internship?.department?._id,
              }}
              isAuthorized={state?.internship?._id ? true : false}
              isVisible={{ all_students: true }}
              authorizedKey="all_students"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            {/* <SquareTabCard
            label="queries"
            iconUrl={`${assestsMentorUrl}/queries.svg`}
            url={`/q/${params.username}/member?a=guide&tab=internship&type=queries`}
            state={{
                ...getQuery.state,
                gid: state?.internship?._id,
                departmentId: state?.internship?.department?._id,
              }}
             isAuthorized={state?.internship?._id ? true : false}
            isVisible={{ bank_details: true }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          /> */}
            <SquareTabCard
              label="meeting"
              iconUrl={`${assestsMentorUrl}/meeting.svg`}
              url={`/q/${params.username}/member?a=guide&tab=internship&type=meeting`}
              state={{
                ...getQuery.state,
                gid: state?.internship?._id,
                departmentId: state?.internship?.department?._id,
              }}
              isAuthorized={state?.internship?._id ? true : false}
              isVisible={{ pending_fees: true }}
              authorizedKey="pending_fees"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="data_export"
              iconUrl={`${assestsUserFinanceUrl}/export.svg`}
              url={`/q/${params.username}/member?a=guide&tab=internship&type=export`}
              state={{
                ...getQuery.state,
                gid: state?.internship?._id,
                departmentId: state?.internship?.department?._id,
              }}
              isAuthorized={state?.internship?._id ? true : false}
              isVisible={{ data_export: true }}
              authorizedKey="data_export"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
          </SquareTabWrapper>
        </>
      ) : searchParams.get("tab") === "mentor" ? (
        <SquareTabWrapper dynamicId="589nbhgh">
          <SquareTabCard
            label="mentees_only"
            iconUrl={`${assestsMentorUrl}/mentee.svg`}
            url={`/q/${params.username}/member?a=guide&tab=mentor&type=mentee`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ all_students: true }}
            authorizedKey="all_students"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="queries"
            iconUrl={`${assestsMentorUrl}/queries.svg`}
            url={`/q/${params.username}/member?a=guide&tab=mentor&type=queries`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ bank_details: true }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="meeting"
            iconUrl={`${assestsMentorUrl}/meeting.svg`}
            url={`/q/${params.username}/member?a=guide&tab=mentor&type=meeting`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ pending_fees: true }}
            authorizedKey="pending_fees"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="data_export"
            iconUrl={`${assestsUserFinanceUrl}/export.svg`}
            url={`/q/${params.username}/member?a=guide&tab=mentor&type=export`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ data_export: true }}
            authorizedKey="data_export"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
        </SquareTabWrapper>
      ) : null}
      {step === 1 ? (
        <InstituteDepartment
          onClose={onCloseStep}
          onSelectStaff={onPracticalGuide}
          title={t("select_project_guide_department")}
          instituteId={instituteId}
          whichType=""
          // selectedId={state.depart_arr}
        />
      ) : step === 2 ? (
        <InstituteDepartment
          onClose={onCloseStep}
          onSelectStaff={onSeminarGuide}
          title={t("select_seminar_guide_department")}
          instituteId={instituteId}
          whichType=""
          // selectedId={state.depart_arr}
        />
      ) : step === 3 ? (
        <InstituteDepartment
          onClose={onCloseStep}
          onSelectStaff={onInternshipGuide}
          title={t("select_internship_guide_department")}
          instituteId={instituteId}
          whichType=""
          // selectedId={state.depart_arr}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default memo(GuideManagerProfile);

const tabList = [
  {
    label: "project_guide",
    id: "project",
    urlKey: "",
  },
  {
    label: "seminar",
    id: "seminar",
    urlKey: "&tab=seminar",
  },
  {
    label: "internship",
    id: "internship",
    urlKey: "&tab=internship",
  },
  // {
  //   label: "mentor",
  //   id: "mentor",
  //   urlKey: "&tab=mentor",
  // },
];
