import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import AddGuideMeeting from "./AddGuideMeeting";

const AddGuideMeetingConfig = ({ onClose, onRefetch, gid }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");

  const onCloseStep = () => {
    setStep("");
  };
  return (
    <>
      {step === 1 ? (
        <AddGuideMeeting
          onClose={onCloseStep}
          mid={gid}
          onRefetch={onRefetch}
          type="RECORD"
          onParentClose={onClose}
        />
      ) : step === 2 ? (
        <AddGuideMeeting
          onClose={onCloseStep}
          mid={gid}
          onRefetch={onRefetch}
          type="SCHEDULE"
          onParentClose={onClose}
        />
      ) : (
        <ModalMenu onClose={onClose}>
          <h6
            onClick={() => {
              setStep(1);
            }}
          >
            {t("record_meeting")}
          </h6>
          <h6
            onClick={() => {
              setStep(2);
            }}
          >
            {t("schedule_meeting")}
          </h6>
        </ModalMenu>
      )}
    </>
  );
};

export default AddGuideMeetingConfig;
