import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { useGuideMeetinDetail } from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInputView from "../../../Ui/Input/FormInputView";
import dayjs from "dayjs";
import { useState } from "react";
import GuideMeetingDetailCard from "./GuideMeetingDetailCard";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddGuideMeeting from "./AddGuideMeeting";

const GuideMeetingDetail = ({ gid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [open, setOpen] = useState("");
  const {
    guideMeetinDetail,
    guideMeetinDetailLoading,
    guideMeetinDetailRefetch,
  } = useGuideMeetinDetail({
    data: {
      meid: getQuery.state?.meeting_detail?._id,
    },
    skip: !getQuery.state?.meeting_detail?._id,
  });

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("meeting_detail")}</h6>

          {guideMeetinDetail?.creation_status === "SCHEDULE" ? (
            <CreateButton
              label="update"
              onAction={onToggle}
              customStyle={{
                margin: "0",
                padding: "0.3rem 1rem",
              }}
            />
          ) : null}
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <FormInputView
          label={`${t("agenda")}: `}
          customFormInputLabel={{
            fontWeight: "500",
          }}
          value={guideMeetinDetail?.agenda ?? ""}
        />
        <FormInputView
          label={`${t("summary")}: `}
          customFormInputLabel={{
            fontWeight: "500",
          }}
          value={guideMeetinDetail?.summary ?? ""}
          customFormInputContainer={{
            alignItems: "flex-start",
          }}
        />
        <FormInputView
          label={`${t("discussion")}: `}
          customFormInputLabel={{
            fontWeight: "500",
          }}
          value={guideMeetinDetail?.discussion ?? ""}
          customFormInputContainer={{
            alignItems: "flex-start",
          }}
        />
        <FormInputView
          label={`${t("date")}: `}
          customFormInputLabel={{
            fontWeight: "500",
          }}
          value={dayjs(guideMeetinDetail?.created_at).format("DD/MM/YYYY")}
        />
        {guideMeetinDetail?.present_mentees?.length > 0 ||
        guideMeetinDetail?.absent_mentees?.length > 0 ? (
          <div className={style.fm_header_container}>
            <h6>{t("total_attendance")}</h6>
          </div>
        ) : null}

        {guideMeetinDetail?.present_mentees?.map((student) => (
          <GuideMeetingDetailCard
            student={student}
            key={student?._id}
            isActive="present"
          />
        ))}
        {guideMeetinDetail?.absent_mentees?.map((student) => (
          <GuideMeetingDetailCard
            student={student}
            key={student?._id}
            isActive="absent"
          />
        ))}
      </UserMemberContentWrapper>
      {guideMeetinDetailLoading && <QvipleLoader />}
      {open && (
        <AddGuideMeeting
          onClose={onToggle}
          mid={gid}
          onRefetch={guideMeetinDetailRefetch}
          type="EDIT"
          meetingDetail={getQuery.state?.meeting_detail}
          isEdit
        />
      )}
    </>
  );
};

export default GuideMeetingDetail;
